var graceChecker = require('@/common/graceChecker.js');
export default {
	data() {
		return {
			phone: '',
			code: '',
			id_card_name: '',
			id_card: '',
			second: 90,
			showText: true,
			pay_paw: '',
			pay_pawa: '',
			agreeCheck: false
		};
	},
	onShow() {
		var user = uni.getStorageSync('uerInfo');
		if (user == '') {






			uni.redirectTo({
				url: '/pages/member/login?isBack=0'
			});

			return;
		}
	},
	onLoad() {},
	methods: {
		dump(url) {
			uni.navigateTo({
				url: url
			});
		},
		checkChange(e) {
			this.agreeCheck = e.detail.value;
		},
		sendCode() {
			//发送验证码
			var th = this;
			if (th.phone == '') {
				uni.showToast({
					icon: 'none',
					title: '请填写正确的手机号码'
				});
				return false;
			}

			//发送验证码
			th.$request.common
				.sendCode({
					phone: th.phone,
					type: 1
				})
				.then(data => {
					uni.hideLoading();
					if (data.status == 1) {
						th.showText = false;
						var interval = setInterval(() => {
							let times = --th.second;
							th.second = times < 10 ? '0' + times : times; //小于10秒补 0
						}, 1000);
						setTimeout(() => {
							clearInterval(interval);
							th.second = 90;
							th.showText = true;
						}, 90000);
					} else {
						uni.showToast({
							icon: 'none',
							title: data.msg
						});
					}
				})
				.catch(err => {
					uni.hideLoading();
					//消息异常
					uni.showToast({
						icon: 'none',
						title: '数据加载异常'
					});
				});
		},
		formSubmit(e) {
			var _this = this;
			if (!_this.agreeCheck) {
				uni.showToast({
					title: "请选阅读并同意用户协议说明",
					icon: 'none'
				});
				return false;
			}
			//定义表单规则
			var rule = [{
					name: 'phone',
					checkType: 'phoneno',
					errorMsg: '电话号码不正确'
				},
				{
					name: 'code',
					checkType: 'string',
					checkRule: '6,6',
					errorMsg: '验证码6个字符'
				},
				{
					name: 'id_card_name',
					checkType: 'string',
					checkRule: '1,100',
					errorMsg: '请输入正确的姓名'
				},
				{
					name: 'id_card',
					checkType: 'string',
					checkRule: '18,18',
					errorMsg: '请输入正确的身份证号码'
				},
				{
					name: 'pay_paw',
					checkType: 'string',
					checkRule: '6,20',
					errorMsg: '密码为6-20个字符'
				},
				{
					name: 'pay_pawa',
					checkType: 'same',
					checkRule: _this.pay_paw,
					errorMsg: '两次密码不相同'
				}
			];
			//进行表单检查
			var formData = e.detail.value;

			var checkRes = graceChecker.check(formData, rule);
			if (checkRes) {
				uni.showLoading({
					title: '数据提交中'
				});
				_this.$request.user
					.setdatum(formData)
					.then(data => {
						uni.hideLoading();
						if (data.status == 1) {
							this.$store.commit('setInformation');

							var user = uni.getStorageSync('uerInfo');
							var user_pay_state = user.user_pay_state; //支付信息
							if (user_pay_state != 1) {
								uni.navigateTo({
									url: '/pages/account/setCard?source=1'
								});
							} else {
								uni.switchTab({
									url: '/pages/member/center'
								});
							}

						} else {
							uni.showToast({
								icon: 'none',
								title: data.msg
							});
						}
					})
					.catch(err => {
						uni.hideLoading();
						//消息异常
						uni.showToast({
							icon: 'none',
							title: '数据加载异常'
						});
					});
			} else {
				uni.showToast({
					title: graceChecker.error,
					icon: 'none'
				});
			}
		}
	}
};
